        i0.ɵɵelementStart(3, "p");
        i0.ɵɵtext(4, "Gillian Harding-Russell was born in Toronto, Ontario in 1952. She grew up in St. Jean-sur-Richelieu and completed her BA (Hons.) and MA from McGill (dissertation on the nineteenth century novel) in 1974. For her Ph.D. she attended the University of Saskatchewan, and wrote her dissertation on postmodern poetry, focussing on the mythopoeic element in the work of Gwendolyn MacEwan and Michael Ondaatje (1986). ");
