        i0.ɵɵelementStart(22, "p");
        i0.ɵɵtext(23, "For a few years, Gillian Harding-Russell worked as a teaching sessional for the English and Sociology departments at the University of Regina, and Creative Writing through University Extension for Continuing Education. She has won first place in the Short Literary Awards for Saskatchewan several times (for \u201CDusk and Dawn: a yard story,\u201D1986, \u201CFlowers Have Feet,\u201D 1991, \u201CIn the Basement of the Plains,\u201D 1993, \u201CNothing Prepared me for Winter,\u201D 2005, \u201CRoses, Lovers and God\u201D 2006). Her poems have been once shortlisted for the CBC award in 2005 (for a sequence of poems entitled \u201CSignatures of Place\u201D), and three times for ");
