        i0.ɵɵtext(26, " Winston Collins Award. More recently a sequence \u201CWe came to find what was real, and to be afraid\u201D and \u201CEnhanced woods\u201D won second place in the GritLit contests (2012 and 2013 respectively). Also, the poetry sequences \u201CWhere the days and nights are equal length\u201D and \u201CProud Men Do Not Listen\u201D were shortlisted for ");
